.App {
  text-align: center;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  text-decoration: none;
  color: inherit;
}

.navbar {
  padding: 40px 10px;
}
.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  list-style: none;
  display: inline-block;
  margin: 0 15px;
}

.navbar li a {
  display: block;
  font-size: 1.2em;
  font-weight: 500;
}

.title {
  font-size: 4em;
  font-weight: 700;
}

h2,
h3 {
  font-weight: 600;
}

.myRole {
  font-size: 1.5em;
}

.social {
  margin: 15px 0;
}
.social a {
  display: inline;
  vertical-align: middle;
  font-size: 1.2em;
}

.works {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.repo-card-div {
  color: rgb(88, 96, 105);
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px -15px;
  padding: 2rem;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.repo-card-div:hover {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px -10px;
}

.repo-stats {
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  font-size: 13px;
  color: rgb(106, 115, 125);
}

.repo-left-stat {
  -webkit-box-flex: 1;
  flex-grow: 1;
  display: flex;
}

.language-color {
  width: 10px;
  height: 10px;
  background-color: blue;
  margin-right: 0.25rem;
  border-radius: 100%;
}

.repo-left-stat span {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  margin-right: 0.75rem;
}

.repo-name-div {
  display: flex;
  align-items: center;
}

.repo-svg {
  margin-right: 0.5rem;
  min-width: 16px;
}

.repo-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  color: rgb(36, 41, 46);
  margin-bottom: 0.75rem;
  font-size: 25px;
  font-weight: 700;
  letter-spacing: -0.5px;
  overflow: hidden;
  margin: 0px;
}

.repo-star-svg {
  margin-right: 0.3rem;
}

.repo-description {
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 1.2em;
  padding: 1rem 0;
  margin: 0;
}

.repo-right-stat p {
  margin: 0;
  padding: 0;
}

.experience__left,
.experience__right {
  width: 49%;
}

.about__left p {
  font-size: 1.2em;
}

#mySvg {
  width: 100%;
  height: auto;
  padding: 4em 0;
}

.role {
  font-size: 1.2em;
  margin: 0;
  padding: 0;
}

.period {
  color: #666;
}

.resumeDownloadBtn {
  padding: 10px 20px;
  border: solid #ccc 1px;
  border-radius: 25px;
  cursor: pointer;
  display: inline-block;

  font-weight: bold;
}

.resumeDownloadBtn:hover {
  background: #000;
  color: #fff;
  border-color: #000;
}

@media (max-width: 767px) {
  .navbar {
    padding: 25px 10px;
  }
  .navbar li {
    margin: 0 10px;
  }
  .navbar li a {
    font-size: 0.9em;
  }
  .title {
    font-size: 3em;
  }
  .experience,
  .works {
    margin-bottom: 30px;
  }
  .experience__left {
    padding: 20px 0;
  }
  .about__left,
  .about__right,
  .experience__left,
  .experience__right {
    width: 100%;
  }
}
